.calender-month-table {
  table-layout: fixed;
  word-break: break-all;
  height: 100%;
  width: 100%;
  font-size: 10px; }
  .calender-month-table td {
    width: 14.28571%;
    vertical-align: middle;
    text-align: center;
    position: relative;
    cursor: default; }
    .calender-month-table td span {
      visibility: hidden;
      background-color: #9dfca9;
      color: black;
      text-align: center;
      padding: 5px 0;
      border-radius: 2px;
      /*定位*/
      position: absolute;
      z-index: 1;
      -webkit-border-radius: 2px;
      -moz-border-radius: 2px;
      -ms-border-radius: 2px;
      -o-border-radius: 2px; }
    .calender-month-table td:hover span {
      visibility: visible;
      width: 120px;
      left: 25px;
      top: -30px; }

